@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&family=Roboto+Condensed:ital,wght@1,400;1,700&display=swap);
header{
    height: 80vh;
    overflow: hidden;
    margin-top: 8rem;
}
.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}



.cta {
    margin-top: 2rem;
    display: flex;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    justify-content: center;
}

.header__social{
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}
.header__social::after{
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}
.me{
    background: linear-gradient(var(--color-primary),transparent);
    width: 20rem;
    height: 25rem;
    position: absolute;
    left: calc(50% - 10rem);
    margin-top: 3rem;
    border-radius:12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 0.5rem 3rem 2rem;
}
.me img{
    height: auto;
    width: 100%;
}

.scroll__down{
    position: absolute;
    right: -2.3rem;
    bottom: 8rem;
    margin-right: 3rem;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    font-weight: 300;
    font-size: 1rem;
}

@media screen and (max-width: 1024px) {
    header{
        height: 65vh;
        
    }
}
@media screen and (max-width: 600px) {
    header{
        height: 100vh;
    }
    .header__social,.scroll__down{
        display: none;
    }
    
}
nav{
    background: rgba(0,0, 0, 0.3);
    width: -webkit-max-content;
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
}
nav a{
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;
}

nav a:hover{
    background: rgba(0,0,0,0.3);
}
nav a.active{
    background: var(--color-bg);
    color: var(--color-white);
}
.about__container{
    display: grid;
    grid-template-columns: 35% 50%;
    grid-gap: 15%;
    gap: 15%;
}

.about__me{
    width: 90%;
    aspect-ratio:1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg,transparent,var(--color-primary),transparent);
    display: grid;
    place-items: center;
    margin-left: 4rem;
}

.about__me-image{
    border-radius: 2rem;
    overflow: hidden;
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
    transition: var(--transition);
}

.about__me-image:hover{
    -webkit-transform: rotate(0);
            transform: rotate(0);
}


.about__cards{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 1.5rem;
    gap: 1.5rem;
    margin-right: 3rem;
    margin-left: 3rem;
}

.about__card{
    background: var(--color-bg-varient);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding:2rem;
    text-align: center;
    transition: var(--transition);
}

.about__card:hover{
    background: transparent;
    border-color: var(--color-bg-varient);
    cursor: default;
}
.about__icon{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card small{
    font-size: 0.7rem;
    color: var(--color-light);
}

.about__content p{
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
    margin-right: 3rem;
    margin-left: 2.5rem;
}

.btn{
    margin-right: 3rem;
    margin-left: 2.5rem;
}



@media screen and (max-width: 1024px) {
   .about__container{
    grid-template-columns: 1fr;
    grid-gap: 0;
    gap: 0;
   }

   .about__me{
    width: 50%;
    margin: 2rem auto 4rem;
   }

   .about__content p{
    margin: 1rem 0 1.5rem;
   }
}
@media screen and (max-width: 600px) {
   .about__me{
        width: 65%;
        margin: 0 auto 3rem;
    }
    .about__cards{
        grid-template-columns: 1fr 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }
    .about__content p{
        margin:1.5rem 0;
    } 
    
}
.experience__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2rm;
    gap: 2rm;
    margin-left: 3rem;
    margin-right: 3rem;
   
}

.experience__container > div{
    background: var(--color-bg-varient);
    padding:2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    margin-left: 3rem;
    margin-right: 3rem;
}

.experience__container > div:hover{
    background: transparent;
    border-color: var(--color-bg-varient);
    cursor: default;
}

.experience__container > div h3{
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}

.experience__content{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 2rem;
    row-gap: 2rem;
}

.experience__details{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
}

.experience__details-icon{
    margin-top: 6px;
    color: var(--color-primary);
}



@media screen and (max-width:1024px) {
    .experience__container{
        grid-template-columns: 1fr;
    }
    .experience__container > div{
       width:80%;
       padding: 2rem;
       margin: 0 auto;
    }

    .experience__content{
        padding: 1rem;
    }
    
}

@media screen and (max-width:600px) {
    .experience__container{
        grid-gap: 1rem;
        gap: 1rem;
    }
    .experience__container > div{
        width: 100%;
        padding:2rem 1rem ;
    }
    
}
.project__container{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-row-gap: 2.5rem;
    row-gap: 2.5rem;
   
}

.project__item{
    background: var(--color-bg-varient);
    padding: 1.3rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
    margin-left: 1rem;
    margin-right: 1rem;
   
}

.project__item:hover{
    border-color: var(--color-primary-varient);
    background: transparent;
}

.project__item-image{
    border-radius: 1.5rem;
    overflow: hidden;
}

.project__item h3{
    margin: 1.2rem 0.2rem;
}

.project_-item-cta{
     display: flex;
     grid-gap: 1rem;
     gap: 1rem;
     margin-bottom: 1rem;
}


@media screen and (max-width:1024px) {
    .project__container{
        grid-template-columns: 1fr 1fr;
        grid-gap: 1.2rem;
        gap: 1.2rem;
    }
}
@media screen and (max-width:600px) {
    .project__container{
        grid-template-columns: 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }
}
.container.contact__container{
    width: 58%;
    display: grid;
    grid-template-columns: 30% 58%;
    grid-gap: 12%;
    gap: 12%;
}

.contact__options{
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}
.contact__option{
    background: var(--color-bg-varient);
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 1px solid transparent;
    transition: var(--transition);
}

.contact__option:hover{
    background: transparent;
    border-color: var(--color-primary-varient);
}
.contact__option-icon{
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}

.contact__option a{
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.8rem;
}

form{
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

input,
textarea{
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: transparent;
    border: 3px solid var(--color-bg-varient);
    resize: none;
    color: var(--color-white);
}

@media screen and (max-width:1024px) {
    .container.contact__container{
        grid-template-columns: 1fr;
        grid-gap: 2rem;
        gap: 2rem;
    }
}

@media screen and (max-width:1024px) {
    .container.contact__container{
        width: var(--container-width-sm);
    }
}
footer{
    background: var(--color-primary);
    padding: 3rem 0;
    text-align: center;
    font-size: 1rem;
    margin-top: 7rem;
}
footer a{
    color: var(--color-bg);
}

.permalinks{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    grid-gap: 2rem;
    gap: 2rem;
    margin: 0 auto 3rem;
}

.footer__socials{
    display: flex;
    justify-content: center;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 4rem;
}

.footer__socials a{
    background: var(--color-bg);
    color: var(--color-white);
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
}

.footer__socials a:hover{
    background: transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
}

.footer__copyright{
    margin-bottom: 4rem;
    color: var(--color-bg);
}



@media screen and (max-width:600px) {
.permalinks{
    flex-direction: column;
    grid-gap: 1.5rem;
    gap: 1.5rem;
}

.footer__socials{
    margin-bottom: 2.6rem;
}
    
}
*{
    margin:0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}
:root{
    --color-bg:#1f1f38;
    --color-bg-varient:#2c2c6c;
    --color-primary:#4db5ff;
    --color-white:#fff;
    --color-light:rgba(255,255,255,0.6);


    --transition: all 400ms ease;

    --container-width-lg:75%;
    --container-width-md:86%;
    --container-width-sm:90%;
}
html{
    scroll-behavior: smooth;
}
::-webkit-scrollbar{
    display:none;
}
body{
  font-family: 'Poppins', sans-serif;
  background: #1f1f38;
  background: var(--color-bg);
  color: #fff;
  color: var(--color-white);
  line-height: 1.7;
  background-image: url(%7Byuvrajput12%7D.github.io/%7BPortfolioApp%7D/static/media/lines-png-16257.d704a7ae.png);
}
.container{
    width: 75%;
    width: var(--container-width-lg);
    margin: 0 auto;
}
h1,h2,h3,h4,h5{
    font-weight: 500;
}
h1{
    font-size: 2.5rem;
}
section{
    margin-top: 8rem;
}
section > h2, section > h5{
    text-align: center;
    color: rgba(255,255,255,0.6);
    color: var(--color-light);
}
section > h2{
    color: #4db5ff;
    color: var(--color-primary);
    margin-bottom: 2rem;
    font-size: 3rem;
}
.text-light {
    color: rgba(255,255,255,0.6);
    color: var(--color-light);
}
a {
    color: #4db5ff;
    color: var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}
a:hover {
    color: #fff;
    color: var(--color-white);
}
.btn {
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color: #4db5ff;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid #4db5ff;
    border: 1px solid var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}
.btn:hover {
    background: #fff;
    background: var(--color-white);
    color: #1f1f38;
    color: var(--color-bg);
    border-color: transparent;
}
.btn-primary {
    background: #4db5ff;
    background: var(--color-primary);
    color: #1f1f38;
    color: var(--color-bg);
}
img {
    display: block;
    width: 100%;
    object-fit: cover;
}


@media screen and (max-width:1024px) {
 .container{
    width: 86%;
    width: var(--container-width-md);
 }
 section{
    margin-top: 6rem;
 }
}
@media screen and (max-width:600px) {
 .container{
    width: 90%;
    width: var(--container-width-sm);
 }
 section > h2 {
    margin-top: 1rem;
 }
}
